import Vue from 'vue'
import Router from 'vue-router'
import index_1 from '@/dayun/index_1'

Vue.use(Router)

export default new Router({
	routes: [{
			path: '/',
			redirect: '/home'
		},
		{
			path: '/home',
			component: resolve => {
				require(['@/dayun/home'], resolve)
			}

		},

		{
			path: '/chedui',
			component: resolve => {
				require(['@/dayun/chedui'], resolve)
			},
			children: [{
					path: '/',
					redirect: '/chedui/chedui-1'
				},
				{
					path: '/chedui/chedui-1',
					component: resolve => {
						require(['@/dayun/child/chedui-1'], resolve)
					},
				},
				{
					path: '/chedui/chedui-2',
					component: resolve => {
						require(['@/dayun/child/chedui-2'], resolve)
					}
				},
				{
					path: '/chedui/chedui-3',
					component: resolve => {
						require(['@/dayun/child/chedui-3'], resolve)
					}
				},
				{
					path: '/chedui/chedui-1/chedui-1-1',
					component: resolve => {
						require(['@/dayun/child/chedui-1-1/chedui-1-1'], resolve)
					}
				},
				{
					path: '/chedui/chedui-1/chedui-1-2',
					component: resolve => {
						require(['@/dayun/child/chedui-1-1/chedui-1-2'], resolve)
					}
				},
				{
					path: '/chedui/chedui-1/chedui-1-3',
					component: resolve => {
						require(['@/dayun/child/chedui-1-1/chedui-1-3'], resolve)
					}
				},
			]
		},

		{
			path: '/yundan',
			component: resolve => {
				require(['@/dayun/yundan'], resolve)
			}
		},
		{
			path: '/fukuan',
			component: resolve => {
				require(['@/dayun/fukuan'], resolve)
			}
		},
		{
			path: '/fapiao',
			component: resolve => {
				require(['@/dayun/fapiao'], resolve)
			}
		},
		{
			path: '/yewu',
			component: resolve => {
				require(['@/dayun/yewu'], resolve)
			}
		},

		{
			path: '/zhanghu',
			component: resolve => {
				require(['@/dayun/zhanghu'], resolve)
			}
		},

		{
			path: '/cz',
			component: resolve => {
				require(['@/dayun/child/cz'], resolve)
			}
		},
		{
			path: '/navBar-1',
			component: resolve => {
				require(['@/dayun/navBar-1'], resolve)
			}
		},
		{
			path: '/tx',
			component: resolve => {
				require(['@/dayun/child/tx'], resolve)
			},
			children: [{
			path: '/',
			redirect: '/company'
		},{
				path: '/company',
				component: resolve => {
					require(['@/dayun/child/chedui-1-1/company'], resolve)
				}
			},{
				path: '/revise',
				component: resolve => {
					require(['@/dayun/child/chedui-1-1/revise'], resolve)
				}
			},{
				path: '/two',
				component: resolve => {
					require(['@/dayun/child/chedui-1-1/two'], resolve)
				}
			},{
				path: '/three',
				component: resolve => {
					require(['@/dayun/child/chedui-1-1/three'], resolve)
				}
			},{
				path: '/four',
				component: resolve => {
					require(['@/dayun/child/chedui-1-1/four'], resolve)
				}
			},{
				path: '/five',
				component: resolve => {
					require(['@/dayun/child/chedui-1-1/five'], resolve)
				}
			}]
		},
		{
			path: '/news',
			component: resolve => {
				require(['@/dayun/child/chedui-1-1/news'], resolve)
			}
		},
		
		{
			path: '/zfsm',
			component: resolve => {
				require(['@/dayun/child/zfsm'], resolve)
			}
		},
		{
			path: '/szmx',
			component: resolve => {
				require(['@/dayun/child/szmx'], resolve)
			}
		},
		{
			path: '/xfmx',
			component: resolve => {
				require(['@/dayun/child/xfmx'], resolve)
			}
		},

		{
			path: '/wrong',
			component: resolve => {

				require(['@/dayun/wrong'], resolve)
			}
		},
		{
			path: '/cha',
			component: resolve => {
				require(['@/dayun/cha'], resolve)
			}
		},
		{
			path: '/zhuce',
			component: resolve => {
				require(['@/page/zhuce'], resolve)
			}
		},
		{
			path: '/wangji',
			component: resolve => {
				require(['@/page/wangji'], resolve)
			},
			children: [{
					path: 'xiu1',
					component: resolve => {
						require(['@/page/xiu1'], resolve)
					}
				},
				{
					path: 'xiu2',
					component: resolve => {
						require(['@/page/xiu2'], resolve)
					}
				}
			]
		},
	]
})


